.button {
    padding: 5px 24px 5px 24px;
    border: none;
    outline: none;
    border-radius: 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    color: white;
}

.disabled {
    background-color: rgba(0, 77, 153, 1);
}

.secondary {
    background-color: rgba(255, 255, 255, 1);
    border: 1px solid rgba(0, 102, 204, 1);
    color: rgba(0, 102, 204, 1);
}

.default {
    background-color: rgba(0, 128, 255, 1);
}

.red {
    background-color: rgba(255, 26, 71, 1);
}

